body {
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    font-size: $font-size-paragraph;
    color: #777777;
    letter-spacing: .2rem; }

.title-first {
    color: $white;
    &__main {
        display: block;
        font-weight: 900;
        font-size: 6rem;
        line-height: 6rem;
        letter-spacing: 1rem;
        margin-bottom: 1rem;
        animation-name: leftToRight;
        animation-duration: 3s;
        animation-delay: 0.5s;
        animation-timing-function: ease;
        backface-visibility: hidden;
        @include respond(tab-land) {
            font-size: 4rem; }
        @include respond(tab-port) {
            font-size: 3.8rem;
            letter-spacing: .5rem;
            margin-bottom: 0; } }

    &__secondary {
        display: block;
        font-weight: 100;
        font-size: 3.5rem;
        line-height: 4.8rem;
        letter-spacing: .5rem;
        animation-name: rightToLeft;
        animation-duration: 3s;
        animation-delay: 0.5s;
        animation-timing-function: ease;
        backface-visibility: hidden;
        margin-bottom: 6.5rem;
        @include respond(tab-land) {
            font-size: 3rem;
            margin-bottom: 3.5rem; }
        @include respond(tab-port) {
            font-size: 2.7rem;
            letter-spacing: .2rem; } } }

// TEXT GRADIENT
.title-secondary {
    display: inline-block;
    text-align: center;
    font-size: $font-size-h2;
    letter-spacing: 1rem;
    text-transform: uppercase;
    font-weight: 800;
    background: linear-gradient(90.05deg, rgba($brown-light, 0.7) 0.03%, rgba($brown-dark, 0.7) 99.94%);
    -webkit-background-clip: text;
    color: transparent;
    transition: all .4s;
    backface-visibility: hidden;
    @include respond(tab-land) {
        font-size: 3rem; }
    @include respond(tab-port) {
        letter-spacing: .6rem; }
    // &:hover
    //     transform: skewY(2deg) skewX(15deg)
    //     text-shadow: $shadow-h2
    &_white {
        color: $white; } }

.title-third {
    font-size: $font-size-h3;
    letter-spacing: .2rem;
    text-transform: uppercase;
    font-weight: 700;
    @include respond(tab-land) {
        font-size: 1.8rem; } }

.paragraph {
    font-size: $font-size-paragraph;
    letter-spacing: .2rem;
    line-height: 140%;
    font-weight: 400;
    @include respond(tab-land) {
        font-size: 1.5rem; } }
