.adventure-card {
    padding: 2.5rem;
    background-color: rgba($white, .8);
    border-radius: .5rem;
    box-shadow: $box-shadow;
    text-align: center;
    transition: .3s all;
    &:hover {
        transform: translateY(-1.5rem) scale(1.03); }
    &__icon {
        display: inline-block;
        font-size: 6rem;
        background: linear-gradient(90.05deg, rgba($brown-light, 0.7) 0.03%, rgba($brown-dark, 0.7) 99.94%);
        -webkit-background-clip: text;
        color: transparent; } }
